<template>
    <div class="col-12 mt-3" v-if="divAvailabilityStatus">
        <div class="col-12 border p-2 float-left">
            <label class="font-weight-bold">{{$t('Availability')}}</label>
            <div v-if="statusAvailable" class="col-12 p-0">
                <small><i class="text-success fa fa-circle"></i> {{$t('Available')}}<span id="noOfAvailableItems"></span>-({{ noOfAvailableItems }})</small>
                <div class="col-12 px-0 mt-2" v-if="uniqueItems">
                    <div class="listing">
                        <div class="table-responsive table-fix-header">
                            <table class="table table-bordered dt-responsive nowrap mt-0" id="tblUniqueItems">
                                <thead class="thead-bg">
                                    <tr>
                                        <th class="text-center">
                                            <div class="custom-control custom-checkbox">
                                                <input id="chkAllUniqueItems" class="custom-control-input" type="checkbox" name="checkbox" v-model='allSelected' @click="selectAll($event)" >
                                                <label class="custom-control-label universal-custom-control-label pt-1" for="chkAllUniqueItems"></label>
                                            </div>
                                        </th>
                                        <th class="text-center">{{$t('AssetId')}}</th>
                                        <th class="text-center">{{$t('AvailableItems')}}</th>
                                    </tr>
                                </thead>
                                <tbody >
                                    <tr v-for="(item,index) in GetUniqueProductIdData" :key="index">
                                        <td class="text-center">
                                            <div class="custom-control custom-checkbox">
                                                <input :id='item.UNIQUE_ID' class="custom-control-input" type="checkbox" name="checkbox" v-model='userIds' :value="item.UNIQUE_ID" @change="select(item)">
                                                <label class="custom-control-label universal-custom-control-label pt-1" :for="item.UNIQUE_ID"></label>
                                            </div>
                                        </td>
                                        <td class="text-center">{{ item.NAME }}</td>
                                        <td class="text-center">{{ item.AVAILABLE_QUANTIY }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="statusReserved">
                <small><i class="text-warning fa fa-circle"></i> {{$t('Reserved')}} - <span class="text-primary cursor-pointer" id="resvation" @click="select()">{{$t('ViewReservations')}}</span></small>
                <div class="col-12 px-0" id="divCalendar"></div>
            </div>
            <div v-if="statusUnAvailable">
                <small><i class="text-danger fa fa-circle"></i> {{$t('Not_Available')}}</small>
            </div> 
        </div>
    </div>
</template>
<script>
export default {
    props:{
        GetUniqueProductIdData:{
            type:Array,
            required:true
        },
        noOfAvailableItems:{
            type:Number,
            required:true
        },
        statusAvailable:{
            type:Boolean,
            required:true
        },
        uniqueItems:{
            type:Boolean,
            required:true
        },
        statusReserved:{
            type:Boolean,
            required:true
        },
        statusUnAvailable:{
            type:Boolean,
            required:true
        },
        divAvailabilityStatus:{
            type:Boolean,
            required:true
        }
    },
    created:function(){        
       // console.log(this.GetUniqueProductIdData);
    },
    data(){
        return{
            CheckCatlougeValue:null,
            CheckeduniqueId:'',
            selected: [],
            allSelected: false,
            userIds: []
        }
    },      
    methods:{
        selectAll: function(e) {            
            this.userIds = [];
            this.checkedName=[];
            var DataArray=[];
            if(e.target.checked){
                this.allSelected=true;
            }else{
                this.allSelected=false;
            }            
            if (this.allSelected) {
                for (let user in this.GetUniqueProductIdData) {
                    this.userIds.push(this.GetUniqueProductIdData[user].UNIQUE_ID);
                    this.checkedName.push(this.GetUniqueProductIdData[user].NAME);
                }
            }
            DataArray.push(this.userIds.toString());
            DataArray.push(this.checkedName.toString());
            this.$emit('onUniqueItemClick',DataArray); 
        },  
        select: function(ths) {                      
            this.userIdss = [];
            this.checkedNames=[]; 
            var DataArray=[];
            if(this.GetUniqueProductIdData.length == this.userIdss.length && this.GetUniqueProductIdData.length == this.checkedNames.length){
                   this.allSelected = true;
            }
            else{
                   this.allSelected = false;
            }          
            for (let user in this.GetUniqueProductIdData) {
                if (ths.UNIQUE_ID==this.GetUniqueProductIdData[user].UNIQUE_ID) {
                    this.userIdss.push(this.GetUniqueProductIdData[user].UNIQUE_ID);
                    this.checkedNames.push(this.GetUniqueProductIdData[user].NAME);
                }
            }            
            DataArray.push(this.userIdss.toString());
            DataArray.push(this.checkedNames.toString());
            this.$emit('onUniqueItemClick',DataArray);
        } 
        }
}
</script>